<template>
<v-container>

	<v-toolbar flat color="white">
			<v-toolbar-title>Crud Atma pedia</v-toolbar-title>
			<v-divider class="mx-4" inset vertical></v-divider>
			<!-- <v-spacer></v-spacer> -->
			<v-dialog v-model="dialog" max-width="500px">
				<template v-slot:activator="{ on }">
					<v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
				</template>
				<v-card>
					<v-card-title>
					<span class="headline">{{ formTitle }}</span>
					</v-card-title>
					<v-card-text>
						<v-container>
							<v-row>
								<v-col cols="12" sm="6" md="4">
								<v-text-field v-model="editedItem.name" label="Drink name"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
								<v-select
                                    v-model="editedItem.pilih"
                                    :items="items"
                                    label="Penyajian"
                                ></v-select>
								</v-col>
								<v-col cols="12" sm="6" md="4">
								<v-text-field v-model="editedItem.calories" label="Calories (g)"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
								<v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
								<v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
								</v-col>
							</v-row>
						</v-container>
					</v-card-text>
					<v-card-actions>
						<v-spacer></v-spacer>
						<v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
						<v-btn color="blue darken-1" text @click="save">Save</v-btn>
					</v-card-actions>
				</v-card>
			</v-dialog>
		</v-toolbar>
		
	<v-row>
	<v-col cols="3" v-for="item in minum" :key="item.name">
	<v-card
    max-width="344"
    outlined
	class="d-inline-flex" 
	
  >
    <v-list-item three-line>
      <v-list-item-content>
        <div class="overline mb-4">MINUMAN</div>
        <v-list-item-title class="headline mb-1">{{minum.name}}</v-list-item-title>
		<v-list-item-subtitle>Type: {{minum.type}}</v-list-item-subtitle>
		<v-list-item-subtitle>Fat: {{minum.fat}}</v-list-item-subtitle>
		<v-list-item-subtitle>Carbs: {{minum.carbs}}</v-list-item-subtitle>
		<v-list-item-subtitle>Protein: {{minum.protein}}</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>

    <v-card-actions>
      <v-icon class="mr-1" color="teal" @click="editItem(item)">mdi-silverware</v-icon>
		<v-icon class="mr-1" color="red"  @click="deleteItem(item)">mdi-delete</v-icon>
    </v-card-actions>
  </v-card>
  </v-col>
</v-row>
</v-container>

	
</template>

<script src="https://cdnjs.cloudflare.com/ajax/libs/vue/1.0.26/vue.js"></script>

<script>

	import{minumRef} from '../firebase';

	export default {
		data: () => ({
			cek: -1,
			dialog: false,
			checkDelete:[],
			search: "",
            items: [
                'Es',
                'Panas',
            ],
			headers: [
				{
				text: "Drink (100g serving)",
				align: "left",
				sortable: false,
				value: "name"
				},
				{ text: "Penyajian", value: "pilih" },
				{ text: "Calories (g)", value: "calories" },
				{ text: "Carbs (g)", value: "carbs" },
				{ text: "Protein (g)", value: "protein" },
				{ text: "Actions", value: "action", sortable: false }
			],
			minum: [],
			editedIndex: -1,
			editedItem: {
				name: "",
				pilih: "",
				calories: 0,
				carbs: 0,
				protein: 0
			},
			defaultItem: {
				name: "",
				pilih: "",
				calories: 0,
				carbs: 0,
				protein: 0
			}
		}),

		firebase: {
			minum : minumRef
		},

		computed: {
			formTitle() {
				return this.cek === -1 ? "New Item" : "Edit Item";
			}
		},
		watch: {
			dialog(val) {
				val || this.close();
			}
		},
		methods: {
                close () {this.dialog = false},
                // methods untuk edit
                editItem (item) {
                  this.editedIndex = item['.key']
                  this.cek = 0
                  this.editedItem = Object.assign({}, item)
                  this.dialog = true
                },
                //methods untuk delete
                deleteItem (item) {
                  confirm('Are you sure you want to delete this item?') && minumRef.child((item['.key'])).remove()
                },
                //methods untuk multiple delete
                deleteAll(){
                  console.log(this.selected)
                  for(this.marked=0;this.marked<this.selected.length;this.marked++){
                    console.log(this.marked)
                    minumRef.child(this.selected[this.marked]).remove()
                  }
                  this.selected=[]
                  console.log(this.selected)
                  
                  },
                // methods untuk save
                save() {
                  if(this.cek>-1) {
                    minumRef.child(this.editedIndex).set({name: this.editedItem.name, pilih: this.editedItem.pilih,
                    calories: this.editedItem.calories, carbs:this.editedItem.carbs, protein: this.editedItem.protein})
                    this.clear()
                  } else {
                    minumRef.push({name: this.editedItem.name, pilih: this.editedItem.pilih, 
                    calories: this.editedItem.calories, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
                    this.clear()
                  }
                  this.close()
                },
                clear() {
                  this.editedItem={}
                  this.editedItem.name= ''
                  this.editedItem.pilih= ''
                  this.editedItem.calories= 0
                  this.editedItem.carbs= 0
                  this.editedItem.protein= 0
                  this.cek=-1
                },
        }
}
                </script>